import React, { useState, useEffect } from 'react';
import { 
  Box, 
  Typography, 
  Grid, 
  Paper, 
  Divider,
  Alert,
  CircularProgress
} from '@mui/material';
import { PayPalButtons } from '@paypal/react-paypal-js';

interface PayPalPaymentFormProps {
  order: {
    id: string | number;
    customerName: string;
    customerEmail: string;
    totalAmount: number;
  };
  onPaymentSuccess: (data: any) => void; 
  onPaymentError: (errorMessage: string) => void;
}

const PayPalPaymentForm: React.FC<PayPalPaymentFormProps> = ({ order, onPaymentSuccess, onPaymentError }) => {
  const [error, setError] = useState<string | null>(null);
  const [processing, setProcessing] = useState(false);
  const [succeeded, setSucceeded] = useState(false);
  const [paypalOrderId, setPaypalOrderId] = useState<string | null>(null);

  // Create PayPal order when component mounts
  useEffect(() => {
    const createPayPalOrder = async () => {
      try {
        setProcessing(true);
        const response = await fetch('/api/payments/create-paypal-order', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${localStorage.getItem('token')}`
          },
          body: JSON.stringify({ databaseOrderId: order.id })
        });

        const data = await response.json();

        if (!response.ok) {
          throw new Error(data.error || 'Failed to create PayPal order');
        }

        setPaypalOrderId(data.paypalOrderId);
        setProcessing(false);
      } catch (err: any) {
        setError(err.message);
        setProcessing(false);
        if (onPaymentError) onPaymentError(err.message);
      }
    };

    if (order && order.id) {
      createPayPalOrder();
    }
  }, [order, onPaymentError]);

  // Handle PayPal order capture
  const handleCaptureOrder = async (paypalOrderId: string): Promise<void> => {
    try {
      setProcessing(true);
      const response = await fetch('/api/payments/capture-paypal-order', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${localStorage.getItem('token')}`
        },
        body: JSON.stringify({ 
          paypalOrderId,
          databaseOrderId: order.id 
        })
      });

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.error || 'Failed to capture PayPal payment');
      }

      setSucceeded(true);
      setProcessing(false);
      if (onPaymentSuccess) onPaymentSuccess(data.data);
    } catch (err: any) {
      setError(err.message);
      setProcessing(false);
      if (onPaymentError) onPaymentError(err.message);
    }
  };

  return (
    <Paper sx={{ p: 3, mb: 3 }}>
      <Typography variant="h6" gutterBottom>
        Payment Method
      </Typography>
      
      {error && (
        <Alert severity="error" sx={{ mb: 2 }}>
          {error}
        </Alert>
      )}
      
      {succeeded ? (
        <Alert severity="success" sx={{ mb: 2 }}>
          Payment successful! Your order has been processed.
        </Alert>
      ) : (
        <Box>
          <Grid container spacing={3}>
            <Grid item xs={12}>
              <Divider sx={{ mb: 2 }} />
              <Typography variant="subtitle1" gutterBottom>
                Order Summary
              </Typography>
              <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                <Typography variant="body2">Subtotal</Typography>
                <Typography variant="body2">${order?.totalAmount?.toFixed(2)}</Typography>
              </Box>
              <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 2 }}>
                <Typography variant="subtitle2">Total</Typography>
                <Typography variant="subtitle2" color="primary">${order?.totalAmount?.toFixed(2)}</Typography>
              </Box>
            </Grid>
            
            <Grid item xs={12}>
              {processing ? (
                <Box sx={{ display: 'flex', justifyContent: 'center', my: 2 }}>
                  <CircularProgress />
                </Box>
              ) : (
                <PayPalButtons
                  disabled={processing || succeeded}
                  forceReRender={[order.totalAmount]}
                  createOrder={(data, actions) => {
                    // If we already created an order on our backend, use that ID
                    if (paypalOrderId) {
                      return Promise.resolve(paypalOrderId);
                    }
                    
                    // Fallback to client-side order creation (not recommended for production)
                    return actions.order.create({
                      intent: "CAPTURE",
                      purchase_units: [
                        {
                          amount: {
                            value: order.totalAmount.toString(),
                            currency_code: "USD"
                          },
                          description: `Order #${order.id}`
                        },
                      ],
                    });
                  }}
                  onApprove={(data, actions) => {
                    // Capture the order on our backend
                    return handleCaptureOrder(data.orderID);
                  }}
                  onError={(err) => {
                    setError('PayPal payment failed. Please try again or use a different payment method.');
                    if (onPaymentError) onPaymentError('PayPal payment failed');
                    console.error('PayPal Error:', err);
                  }}
                  style={{ layout: "vertical" }}
                />
              )}
            </Grid>
          </Grid>
        </Box>
      )}
    </Paper>
  );
};

export default PayPalPaymentForm;
